
.rd-audio-player-container {
    display: inline-block;
}
.rd-audio-player-wrapper {
    width: 20rem;
    background: #fff;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    height: 3rem;
    border-radius: .2rem;
    box-shadow: 0 0 .5rem #c1c1c1;  
}
.rd-audio-cover {
    position: relative;
    width: 3rem;
    -ms-flex-negative: 0;
        flex-shrink: 0;
    background-size: cover;
    background-color: #CDDC39;
}
.rd-audio-contrl-wrapper {
    width: 100%;
    padding: 0 .5rem;
}
.rd-audio-play {
    width: 3rem;
    -ms-flex-negative: 0;
        flex-shrink: 0;
}
.rd-audio-play-btn {
    position: absolute;
    background: rgba(255, 255, 255, 0.8);
    border: none;
    width: 2rem;
    height: 2rem;
    top: 50%;
    left: 50%;
    margin: -1rem 0 0 -1rem;
    border-radius: 50%;
    outline: none;
}
.rd-audio-play-btn-icon {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 1rem;
    height: 1rem;
    margin: -.5rem 0 0 -.4rem;
}
.rd-audio-play-btn-icon.pause {
    margin: -.5rem 0 0 -.5rem; 
}
.rd-audio-title {
    height: 2rem;
    font-size: .8rem;
    line-height: 1rem;
    padding: .7rem 0 0 0;
    box-sizing: border-box;
}
.rd-audio-slider {
    width: 100%;
    height: .75rem;
    background: #fff;
    padding: 0 .25rem;
    box-sizing: border-box;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.rd-audio-slider-rail {
    position: relative;
    width: 100%;
    height: .15rem;
    margin: .35rem 0 0 0;
    background: #9E9E9E;
}
.rd-audio-slider-dot {
    position: absolute;
    height: .5rem;
    width: .5rem;
    background: #ffffff;
    top: 0;
    left: 0;
    border: 1px solid #ccc;
    border-radius: 50%;
    margin: -.25rem 0 0 -.25rem;
}
.rd-audio-slider-time {
    font-size: .75rem;
    -ms-flex-negative: 0;
        flex-shrink: 0;
    margin-left: .5rem;
}
.rd-audio-player-wrapper.large {

}



